.route.home
{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  overflow: hidden;
}

/* BACKGROUND */
.route.home > .background
{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  filter: grayscale(1) contrast(0.2) blur(4px);
  opacity: 0.1;
  transition: filter 0.5s, opacity 0.5s;
}

#root:hover .route.home > .background
{
  opacity: 0.4;
}



/* GLOW */
.route.home > .glow
{
  position: absolute;
  top: 0;
  height: 100%;
  width: 50%;
  opacity: 0.2;
}

.route.home > .glow.left
{
  left: 0;
}

.route.home > .glow.right
{
  right: 0;
}



/* LOGO */
.k
{
  font-size: 8em;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  transition: transform 0.5s, color 0.5s, text-shadow 0.5s, opacity 0.5s;
  line-height: 0.8em;
  color: red;
  opacity: 0.6;
}

#root:hover .k
{
  transform: scale3d(1.25, 1.25, 1.25);
  opacity: 1;
}

#root .k:hover
{
  transform: scale3d(1.5, 1.5, 1.5);
  text-shadow: 0 0 10px rgba(255, 0, 0, 0.6);
}
